import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FlexLayoutModule } from '@angular/flex-layout';
import { RouterModule, Routes } from '@angular/router';
import { MdIconModule } from "@angular/material";

import { AppComponent } from './app.component';
import { SharedModule } from "app/shared/shared.module";
import { HomeComponent } from "app/pages/home/home.component";
import { PagesModule } from "app/pages/pages.module";
import { FormComponent } from "app/pages/form/form.component";
import { OfferComponent } from "app/pages/offer/offer.component";
import { MailComponent } from "app/pages/mail/mail.component";
import { PurchaseComponent } from "app/pages/purchase/purchase.component";
import { ConfirmComponent } from "app/pages/confirm/confirm.component";
import { SearchComponent } from "app/pages/search/search.component";
import { InfoComponent } from "app/pages/info/info.component";
import { ValidityComponent } from "app/pages/validity/validity.component";

const appRoutes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'form', component: FormComponent },
  { path: 'offer', component: OfferComponent },
  { path: 'mail', component: MailComponent },
  { path: 'purchase', component: PurchaseComponent },
  { path: 'confirm', component: ConfirmComponent },
  { path: 'search', component: SearchComponent },
  { path: 'info', component: InfoComponent },
  { path: 'validity', component: ValidityComponent },
];

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule,
    PagesModule,
    MdIconModule,

    FlexLayoutModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



