import { NgModule } from '@angular/core';
import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { MdCardModule, MdButtonModule, MdIconModule, MdInputModule, MdSelectModule, MdDialogModule, MdGridListModule, MdTooltipModule, MdDatepickerModule, MdNativeDateModule } from "@angular/material";
import { FlexLayoutModule } from "@angular/flex-layout";
import { RouterModule } from "@angular/router";

import { HomeComponent } from "app/pages/home/home.component";
import { FormComponent } from "app/pages/form/form.component";
import { OfferComponent } from "app/pages/offer/offer.component";
import { OfferDetailComponent } from "app/pages/offer/offer-detail.component";
import { MailComponent } from "app/pages/mail/mail.component";
import { PurchaseComponent } from "app/pages/purchase/purchase.component";
import { ConfirmComponent } from "app/pages/confirm/confirm.component";
import { ConfirmDocumentComponent } from "app/pages/confirm/confirm-document.component";
import { SearchComponent } from "app/pages/search/search.component";
import { InfoComponent } from "app/pages/info/info.component";
import { ValidityComponent } from "app/pages/validity/validity.component";

@NgModule({
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        RouterModule,
        MdCardModule,
        MdButtonModule,
        MdIconModule,
        FlexLayoutModule,
        MdInputModule,
        MdSelectModule,
        MdDialogModule,
        MdGridListModule,
        MdTooltipModule,
        MdNativeDateModule,
        MdDatepickerModule
    ],
    exports: [
        HomeComponent,
        FormComponent,
        OfferComponent,
        OfferDetailComponent,
        MailComponent,
        PurchaseComponent,
        ConfirmComponent,
        ConfirmDocumentComponent,
        SearchComponent,
        InfoComponent,
        ValidityComponent
    ],
    declarations: [
        HomeComponent,
        FormComponent,
        OfferComponent,
        OfferDetailComponent,
        MailComponent,
        PurchaseComponent,
        ConfirmComponent,
        ConfirmDocumentComponent,
        SearchComponent,
        InfoComponent,
        ValidityComponent
    ],
    entryComponents: [OfferDetailComponent, ConfirmDocumentComponent],
    providers: [],
})
export class PagesModule { }



